body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav {
  flex-shrink: 0;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  position: relative;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
